import { Type_RefFuncKrpano } from '@components/Krpano/types';
import dynamic from 'next/dynamic';
import { memo } from 'react';
import { ControlbarWrapper } from './styled';

const ControlbarCopyright = dynamic(() => import('./Controlbar.Copyright'));
const ControlbarAction = dynamic(() => import('./Controlbar.Action'));
const ControlbarDirectionBtn = dynamic(
  () => import('./Controlbar.DirectionBtn'),
);

export interface I_ControlbarDefaultTheme {
  onVR?: () => void;
  onUp?: () => void;
  onDown?: () => void;
  onLeft?: () => void;
  onRight?: () => void;
  onFullscreen?: () => void;
  onViewNormal?: () => void;
  onStopRotate?: () => void;
  onViewPlannet?: () => void;
  onStartRotate?: () => void;
  onToFirstScene?: () => void;
  onStopVertical?: () => void;
  onViewMirrorBall?: () => void;
  onStopHorizontal?: () => void;
}

export interface Props {
  krpanoRef: Type_RefFuncKrpano | null;
}

export const Controlbar = memo(({ krpanoRef }: Props) => {
  return (
    <ControlbarWrapper id="themeControlbar">
      <ControlbarCopyright />
      <ControlbarDirectionBtn krpanoRef={krpanoRef} />
      <ControlbarAction krpanoRef={krpanoRef} />
    </ControlbarWrapper>
  );
});

export default Controlbar;
